.loadingDots {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .dot {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    border-radius: 50%;
    background-color: #dc143c;
    animation: bounce 1s infinite ease-in-out;
  }
  
  @keyframes bounce {
    0%,
    80%,
    100% {
      transform: scale(0);
    }
    40% {
      transform: scale(1);
    }
  }